
.playlist-container {
    display: flex;
    flex-direction: column;
    margin: 10px;
    border-radius: 8px;
    background-color: var(--color-box-container);
    color: #ffffff;
    width: 100%;
    padding: 32px;
    height: 70vh;
    overflow-y: scroll;
}
.playlist-container::-webkit-scrollbar {
    width: 1px;
}

.playlist-container::-webkit-scrollbar-thumb {
    background-color: transparent;
}
.playlist h1 {
    font-size: 32px;
    margin-bottom: 50px;
}

.playlist h2 {
    font-size: 22px;
    margin-bottom: 30px;
}



.offer__scroll-container {

    margin: 12px 0 0;
    overflow: auto;
}

.offer__scroll-container::-webkit-scrollbar {
    width: 1px;
}

.offer__scroll-container::-webkit-scrollbar-thumb {
    background-color: transparent;
}

.offer__list-item {
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 16px;
    gap: 16px;
    margin-bottom: 16px;
}

.cards {
    position: relative;
    width: 220px;
    height: 200px;
    overflow: hidden;
    border-radius: 8px;
}

.cards span {
    font-size: 20px;
    color: white;
    font-weight: bold;
    position: absolute;
    top: 10px;
    left: 10px;
}

.cards img {
    transform: rotate(30deg);
    width: 140px;
    position: absolute;
    bottom: 0;
    right: 0;
}

.hidden {
    display: none;
}

.grid-container {
    grid-gap: 24px;
    display: grid;
    grid-template-columns: repeat(5, minmax(0, 1fr));
}

.artist-card {
    background-color: var(--artist-box-bg-1);
    border-radius: 8px;
    flex: 1;
    padding: 16px;
    position: relative;
    transition: background-color 0.3s ease;
    width: 100%;
}

.artist-card:hover {
    background-color: var(--artist-box-bg-2);
}

.card-img {
    padding-bottom: 100%;
    position: relative;
    width: 100%;
    margin-bottom: 16px;
}

.card-img .play {
    position: relative;

}

.card-img .play .fa-play {
    position: absolute;
    right: 0;
    top: 160px;
    padding: 18px;
    background-color: #1db954;
    border-radius: 100%;
    font-size: 14px;
    opacity: 0;
    transition: all ease 0.3s;
}

.artist-card:hover .play .fa-play {
    opacity: 1;
    transform: translateY(-20px);
}

.artist-img {

    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.card-text {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    min-height: 62px;
}

.card-text a {
    color: #fff;
}

.card-text .artist-name {
    font-weight: 600;
    font-size: 16px;
}

.card-text .artist-categorie {
    margin-top: 5px;
    font-size: 14px;
    color: var(--color-box-text);
    font-weight: 600;
}

.result-artist {
    padding: 20px 40px;
}


